import React from 'react';
import './App.css';
import copy from 'copy-to-clipboard';
import SpeechRecognition, { useSpeechRecognition } from 'react-speech-recognition';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
function App() {
const noti=()=>{

copy(transcript)

  toast("COPIED....",
  
  {

    position: "top-right",
autoClose: 2900,
hideProgressBar: false,
closeOnClick: true,
pauseOnHover: true,
draggable: true,
progress: undefined,
theme: "dark",
  }
  );

}

const startListing=()=>{
  toast("SAY SOMETHING.........",
  
  {

    position: "top-right",
autoClose: 2900,
hideProgressBar: false,
closeOnClick: true,
pauseOnHover: true,
draggable: true,
progress: undefined,
theme: "dark",
  })
  
  SpeechRecognition.startListening({continuous:true,language:'en-IN'})}
const {transcript,browserSupportsSpeechRecognition }=useSpeechRecognition()
  if (!browserSupportsSpeechRecognition) {
    return null
  }
  return (
<>
<div id='main' >
  <h1>speech to <span> text </span> converter</h1>
  <div id='text' >
    <div id='circle' >
      
<div id='btn1' className='one' >
  
</div>
<div id='btn2' className='one' >
  
</div><div id='btn3' className='one' >
  
  </div>

  </div>

    {transcript}
    </div>

  <div id='btns' >
    
  
  <button className='btn1' onClick={startListing} type="">start</button>
  <button className='btn2' onClick={noti} type="">copy</button>
  
  <button className='btn3' onClick={SpeechRecognition.stopListening} type="">stop</button>
  
  </div>

  

</div>

<ToastContainer 
position="top-right"
autoClose={1900}
hideProgressBar={false}
newestOnTop={false}
closeOnClick
rtl={false}
pauseOnFocusLoss
draggable
pauseOnHover
theme="dark"
  />
</>
  )
}

export default App;
